/**
 * Service principal pour SUPRA-CODE NEURO-PERFORMANCE
 * Gre les interactions avec Firebase et les Cloud Functions
 */

import { db } from './firebase';

class SupraScanService {
  constructor() {
    this.functionsURL = import.meta.env.VITE_FUNCTIONS_URL || 'https://us-central1-supra-scan-app.cloudfunctions.net';
    this.adminKey = import.meta.env.VITE_ADMIN_KEY || 'nWpOn75JdE4qElhGtk6db3eBvjIWUvMo';
  }

  /**
   * Soumet les donnes de l'oprateur et ses rponses
   * @param {Object} data - { operator, answers, meta }
   * @returns {Promise<Object>} Rsultat de la soumission
   */
  async submitOperatorReport(data) {
    try {
      // Validation des donnes
      if (!data.operator?.email || !data.answers) {
        throw new Error('Donnes oprateur ou rponses manquantes');
      }

      const response = await fetch(`${this.functionsURL}/submitOperatorReport`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(data)
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.error || `HTTP ${response.status}`);
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.error('Erreur soumission rapport:', error);
      throw error;
    }
  }

  /**
   * Rcupre un rapport public par token
   * @param {string} token - Token d'accs public
   * @returns {Promise<Object>} Donnes du rapport
   */
  async getPublicReport(token) {
    try {
      if (!token) {
        throw new Error('Token manquant');
      }

      const response = await fetch(`${this.functionsURL}/getPublicReport?token=${encodeURIComponent(token)}`);

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.error || `HTTP ${response.status}`);
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.error('Erreur rcupration rapport public:', error);
      throw error;
    }
  }

  /**
   * Rcupre la liste des rapports (admin)
   * @param {string} adminKey - Cl admin
   * @returns {Promise<Object>} Liste des rapports
   */
  async listReports(adminKey) {
    try {
      if (!adminKey) {
        throw new Error('Cl admin manquante');
      }

      const response = await fetch(`${this.functionsURL}/listReports`, {
        method: 'GET',
        headers: {
          'X-Admin-Key': adminKey
        }
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.error || `HTTP ${response.status}`);
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.error('Erreur rcupration rapports:', error);
      throw error;
    }
  }

  /**
   * Active l'accs complet (future fonctionnalit)
   * @param {string} activationKey - Cl d'activation
   * @param {string} reportId - ID du rapport
   * @returns {Promise<Object>} Rsultat de l'activation
   */
  async activateFullAccess(activationKey, reportId) {
    try {
      const response = await fetch(`${this.functionsURL}/activateFullAccess`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ activationKey, reportId })
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(errorData.error || `HTTP ${response.status}`);
      }

      const result = await response.json();
      return result;
    } catch (error) {
      console.error('Erreur activation accs complet:', error);
      throw error;
    }
  }

  /**
   * Sauvegarde les rponses du questionnaire
   * @param {Object} answers - Rponses du questionnaire
   */
  saveAnswers(answers) {
    try {
      localStorage.setItem('supra_scan_answers', JSON.stringify(answers));
    } catch (error) {
      console.error('Erreur sauvegarde rponses:', error);
    }
  }

  /**
   * Rcupre les rponses sauvegardes
   * @returns {Object} Rponses du questionnaire
   */
  loadAnswers() {
    try {
      const saved = localStorage.getItem('supra_scan_answers');
      return saved ? JSON.parse(saved) : {};
    } catch (error) {
      console.error('Erreur chargement rponses:', error);
      return {};
    }
  }

  /**
   * Efface les rponses sauvegardes
   */
  clearAnswers() {
    try {
      localStorage.removeItem('supra_scan_answers');
    } catch (error) {
      console.error('Erreur effacement rponses:', error);
    }
  }

  /**
   * Sauvegarde les donnes de l'oprateur
   * @param {Object} operator - Donnes de l'oprateur
   */
  saveOperator(operator) {
    try {
      localStorage.setItem('supra_scan_operator', JSON.stringify(operator));
    } catch (error) {
      console.error('Erreur sauvegarde oprateur:', error);
    }
  }

  /**
   * Rcupre les donnes de l'oprateur sauvegardes
   * @returns {Object} Donnes de l'oprateur
   */
  loadOperator() {
    try {
      const saved = localStorage.getItem('supra_scan_operator');
      return saved ? JSON.parse(saved) : null;
    } catch (error) {
      console.error('Erreur chargement oprateur:', error);
      return null;
    }
  }

  /**
   * Efface les donnes de l'oprateur
   */
  clearOperator() {
    try {
      localStorage.removeItem('supra_scan_operator');
    } catch (error) {
      console.error('Erreur effacement oprateur:', error);
    }
  }
}

export const supraScanService = new SupraScanService();

